@import "../../../scss/_variables";

.timeline {
  display: none;
}

@media(min-width: $breakpoint-small) {

  .timeline {
    display: block;
    position: fixed;
    bottom: 20px;
    right: 20px;
    display: flex;
    align-items: flex-end;

    p {
      margin: 0 20px 0 0;
    }
  }

  .progressBar {
    height: 100px;
    width: 60px;
    border-radius: $border-radius;
    background: $orange;
    transform-origin: bottom;
    transform: scaleX(0);
    transition: all 0.3s $cubic-ease;
  }

  @media (min-width: $breakpoint-small) {
    .timeline {
      bottom: 50px;
      right: 50px;
    }
  }
}