@import "../../../scss/_variables";

#editorView {
  position: relative;
  top: 20vh;
  margin: 0 auto;
  width: 90%;
  z-index: 10;
  overflow-x: hidden;
}

@media (min-width: $breakpoint-small) {
  #editorView {
    padding: 0 50px;
    position: fixed;
    top: calc(8vh + 20px);
    right: 20px;
    width: calc(80vw - 60px);
    height: calc((100vh - 8vh) - 40px);
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 1em;
      background-color: $grey-accent;
      border-radius: $border-radius;
      box-shadow: $box-shadow;
    }

    &::-webkit-scrollbar-track {
      box-shadow: $box-shadow;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: $border-radius;
      background-color: $blue;
      outline: 1px solid $dark-accent;
    }
  }
}
